body {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 12px;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  margin: 0;
  padding: 0;
}
img {
  display: block;
  margin: 0;
  // border: 1px solid red;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}
.container {
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  max-width: 320px;

  @media screen and (min-width: $tablet) {
    padding-left: 32px;
    padding-right: 32px;
    max-width: 768px;
  }
  @media screen and (min-width: $desktop) {
    max-width: 1024px;
  }
  // max-width: 1024px;
  // margin-left: auto;
  // margin-right: auto;
}

